import React from "react";
import Link from "gatsby-link";

import {
  blog_list__container,
  bloglist__sub_link,
  blog_list__blog
} from "../assets/styles/modules/blog-list.module.styl";
import {
  common__less_bmargin,
  blog__date,
  common__no_bullets,
  inline_list,
  common__link
} from "../assets/styles/base/_common-typography.module.styl";

const IndexPage = ({ data }) => {
  const { edges: posts } = data.allMarkdownRemark;

  return (
    <div className={blog_list__container}>
      {posts.map(({ node: post }) => {
        const { frontmatter } = post;

        return (
          <div className={blog_list__blog}>
            <h2 className={common__less_bmargin}>
              <Link to={frontmatter.path}>{frontmatter.title}</Link>
            </h2>
            <p className={blog__date}>{frontmatter.date}</p>
            <p>
              {frontmatter.excerpt}{" "}
              <Link to={frontmatter.path} className={bloglist__sub_link}>
                Read more...
              </Link>
            </p>
            <ul className={common__no_bullets}>
              <strong>
                <Link to="/tags">Tags:</Link>
              </strong>{" "}
              [{post.frontmatter.tags.map(tag => {
                return (
                  <li className={inline_list}>
                    <Link className={common__link} to={`/tags/${tag}`}>
                      {tag}
                    </Link>
                  </li>
                );
              })}]
            </ul>
          </div>
        );
      })}
    </div>
  );
};

export const query = graphql`
  query IndexQuery {
    allMarkdownRemark {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            path
            tags
            excerpt
          }
        }
      }
    }
  }
`;

export default IndexPage;



